<template>
  <v-container fluid>
    <v-row>
      <v-col cols="12" class=" ma-2 text-center">
        <h1>My Expenses for {{monthName}} {{year}}</h1>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="3" v-for="category in categories" v-bind:key="category.name">
        <category-with-expenses v-on:newCategoryAdded="fetchCategories" v-bind:category="category" />
      </v-col>
      <button class="add-category-button shadow-effect" @click="$refs.addCategoryModal.openModal()">+</button>
      <add-category-dialog v-on:newCategoryAdded="fetchCategories" ref="addCategoryModal"/>
    </v-row>
  </v-container>
</template>

<script>
import Vue from 'vue'
import CategoryWithExpenses from './CategoryWithExpenses.vue'
import AddCategoryDialog from './AddCategoryDialog.vue'

export default Vue.extend({
  name: 'Main',
  props: {
  },
  components: {
    CategoryWithExpenses,
    AddCategoryDialog
  },
  data () {
    return {
      categories: [],
      month: null,
      year: null
    }
  },
  mounted () {
    this.setDefaultMonthAndYear()
    this.fetchCategories()
  },
  methods: {
    fetchCategories () {
      console.log(process.env.VUE_APP_API_URL)
      fetch(process.env.VUE_APP_API_URL + 'categories')
        .then((response) => response.json())
        .then((data) => {
          this.categories = data
        })
    },
    setDefaultMonthAndYear () {
      const today = new Date()
      this.month = today.getMonth()
      this.year = today.getFullYear()
    }
  },
  computed: {
    monthName () {
      // today.toLocaleString('en-US', { month: 'long' })
      return 'March'
    }
  }
})
</script>
