<template>
  <v-card class="ma-2">
    <v-card-title class="headline" v-text="category.name">
    </v-card-title>
    <v-card-text>
      <expenses :expenses="category.expenses" />
    </v-card-text>
    <v-card-actions>
      <add-expense-dialog
      v-on:newExpenseAdded="fetchCategories"
      ref="addExpenseModal"
      :category="category"
    />
    </v-card-actions>
    <v-progress-linear
        absolute
        bottom
        color="green"
        value="45"
      ></v-progress-linear>
  </v-card>
</template>

<script>
import Vue from 'vue'
import Expenses from './Expenses.vue'
import AddExpenseDialog from './AddExpenseDialog.vue'

export default Vue.extend({
  props: {
    category: {}
  },
  components: {
    Expenses,
    AddExpenseDialog
  },
  data () {
    return {
      newExpenseName: '',
      newExpenseAmount: null
    }
  },
  methods: {
    fetchCategories () {
      this.$emit('newCategoryAdded')
    },
    clearInputs () {
      this.newExpenseName = ''
      this.newExpenseAmount = null
    },
    adjust (color, amount) {
      return '#' + color.replace(/^#/, '').replace(/../g, color => ('0' + Math.min(255, Math.max(0, parseInt(color, 16) + amount)).toString(16)).substr(-2))
    }
  }
})

</script>
